.menu {
    display: grid;
    grid-template-columns: repeat(2, 1fr); /* Всегда 2 колонки */
    gap: 16px;
    padding: 20px;
    justify-content: center;
}

.menu-element {
    border-radius: 10px;
    padding: 10px;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    background-color: #f0f0f0;
    cursor: pointer;
    transition: background-color 0.3s;
    align-self: start; /* Элементы не будут растягиваться */
}

.menu-element:hover {
    background-color: #e0e0e0;
}

.emoji {
    font-size: 40px;
}

.details {
    margin-top: 10px;
}

.name {
    font-size: 16px;
    margin: 0;
}

.price {
    font-size: 14px;
}

.buy-button {
    margin-top: 10px;
    padding: 8px 16px;
    background-color: #4caf50;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

.ingredients {
    margin-top: 15px;
}

.ingredients ul {
    padding: 0;
    list-style-type: none;
    margin: 0;
}

.ingredients li {
    font-size: 12px;
    margin-top: 5px;
}

.toggle-button {
    margin-top: 10px;
    padding: 8px 16px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}
